<template>
  <div v-if="currWidth > 400" class="sketches flexcenter photocontainer">
    <div v-if="isImageBigDisplayed" class="photoModal">
      <FlexBox w="100%" h="100%">
        <BMLoader v-show="isImgLoading" />
        <div v-show="!isImgLoading" :class="'bigImgPlace ' + position">
          <div class="leftSideBigImg"></div>
          <img @load="isImgLoading = false" :src="URL" alt="bigPic" />
          <div class="rightSideBigImg">
            <div class="hideBtn" :onclick="hideModal"></div>
          </div>
        </div>
      </FlexBox>
    </div>
    <div v-for="n in 9" v-bind:key="n" class="sketches1 flexcenter">
      <div
        v-on:click="clickOnImg(n)"
        :style="
          'background-image:url(http://svoyapi.ru/vlada-pages/400/photo/f000' +
          n +
          ');'
        "
        class="sketch"
      >
        <div
          v-if="activePhoto === n"
          class="link-to-project"
          :style="'text-decoration:none;'"
        >
          <div class="shadow flexcenter">full size</div>
        </div>
      </div>
    </div>
    <div v-for="n in 3" v-bind:key="n" class="sketches1 flexcenter">
      <div
        v-on:click="clickOnImg(n + 9)"
        :style="
          'background-image:url(http://svoyapi.ru/vlada-pages/400/photo/f00' +
          (n + 9) +
          ');'
        "
        class="sketch"
      >
        <div
          v-if="activePhoto === n + 9"
          class="link-to-project"
          :style="'text-decoration:none;'"
        >
          <div class="shadow flexcenter">full size</div>
        </div>
      </div>
    </div>
  </div>
  <div v-else class="sketches flexcenter">
    <div v-for="n in 9" v-bind:key="n" class="sketches1 flexcenter">
      <div
        v-on:click="clickOnImg(n)"
        :style="
          'background-image:url(http://svoyapi.ru/vlada-pages/300/photo/f000' +
          n +
          ');'
        "
        class="sketch"
      >
        <div
          v-if="activePhoto === n"
          class="link-to-project"
          :style="'text-decoration:none;'"
        >
          <div class="shadow flexcenter">full size</div>
        </div>
      </div>
    </div>
    <div v-for="n in 3" v-bind:key="n" class="sketches1 flexcenter">
      <div
        v-on:click="clickOnImg(n + 9)"
        :style="
          'background-image:url(http://svoyapi.ru/vlada-pages/300/photo/f00' +
          (n + 9) +
          ');'
        "
        class="sketch"
      >
        <div
          v-if="activePhoto === n + 9"
          class="link-to-project"
          :style="'text-decoration:none;'"
        >
          <div class="shadow flexcenter">full size</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, computed } from "vue";
import { useStore } from "vuex";
import FlexBox from "@/components/containers/FlexBox.vue";
import BMLoader from "@/components/bricks/BMLoader.vue";

const store = useStore();
const activePhoto = ref<number>(0);
const showedPhoto = ref<number>(0);
const isImgLoading = ref<boolean>(false);
const isImageBigDisplayed = ref<boolean>(false);
const currWidth = computed<number>(() => store.state.Status.currWidth);
const currHeight = computed<number>(() => store.state.Status.currHeight);
const screenRatio = computed(() =>
  (currWidth.value / currHeight.value).toFixed(2)
);
const bigImageWidth = computed(() =>
  +screenRatio.value > 0.8316
    ? ((store.state.Status.currHeight * 0.88) / 1.31).toFixed(0)
    : (store.state.Status.currWidth * 0.88).toFixed(0)
);
const position = computed(() => {
  return +screenRatio.value > 0.8316 ? "gorizontal" : "vertical";
});
const URL = computed(() =>
  showedPhoto.value < 10
    ? "https://svoyapi.ru/vlada-pages/" +
      String(bigImageWidth.value) +
      "/photo/f000" +
      String(showedPhoto.value)
    : "https://svoyapi.ru/vlada-pages/" +
      String(bigImageWidth.value) +
      "/photo/f00" +
      String(showedPhoto.value)
);

const clickOnImg = (n: number) => {
  if (n === activePhoto.value) {
    activePhoto.value = 0;
    showedPhoto.value = n;
    isImageBigDisplayed.value = true;
    isImgLoading.value = true;
  } else {
    activePhoto.value = n;
  }
};

const hideModal = () => {
  isImageBigDisplayed.value = false;
};

onMounted(() => {
  const store = useStore();
  setTimeout(() => {
    store.commit("Status/showHeader");
  }, 1000);
});
</script>

<style lang="scss" scoped>
.bigImgPlace {
  height: 88dvh;
  width: calc(67.18vh + 6vh);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.vertical {
  width: 98vw;
  height: 135vw;
}

.vertical img {
  width: 86vw;
  height: calc(86vw * 1.31);
}

.leftSideBigImg,
.rightSideBigImg {
  height: 100%;
  min-width: 5vh;
}
.vertical .leftSideBigImg,
.vertical .rightSideBigImg {
  min-width: 4vw;
  height: calc(86vw * 1.31);
}
.hideBtn {
  height: 5vh;
  min-width: 5vh;
  margin-top: 1vh;
  background-repeat: no-repeat;
  background-size: 100%, auto;
  background-image: url("~@/assets/img/svg/closeSilver.svg");
  cursor: pointer;
}
.vertical .rightSideBigImg .hideBtn {
  min-height: 3vw;
  min-width: 3vw;
  margin-top: 1vw;
}
.link-to-project {
  height: 100%;
  width: 100%;
}
.photocontainer {
  max-width: 1438px;
  margin: 0 auto;
}
.photoModal {
  width: 100vw;
  height: 100dvh;
  position: fixed;
  top: 0;
  background-color: rgb(32, 32, 32);
  opacity: 0.98;
}
.shadow {
  background-color: grey;
  height: 100%;
  width: 100%;
  opacity: 0.3;
  text-decoration: none;
  color: $white;
  font-size: 33px;
}
.sketches {
  background-color: $black;
  width: 90%;
  min-height: min(88vh, calc(100vh - 140px));
  padding: 0 5% 0 5%;
  flex-wrap: wrap;
}
.sketches1 {
  background-color: $black;
  width: 90%;
  height: 100%;
  flex-wrap: wrap;
}
.sketch {
  height: 470px;
  width: 400px;
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center center;
  margin-bottom: 15px;
}

@media only screen and (min-width: 745px) {
  .sketch {
    height: 393px;
    width: 300px;
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center center;
    margin-bottom: 15px;
  }
  .sketches1 {
    justify-content: space-between;
    flex-wrap: wrap;
    width: 330px;
  }
}

@media only screen and (max-width: 745px) and (min-width: 441px) {
  .sketch {
    height: 57vw;
    width: 48%;
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center center;
    margin-bottom: 15px;
  }
}

@media only screen and (max-width: 440px) {
  .sketches {
    background-color: black;
    width: 100%;
    min-height: min(88vh, calc(100vh - 140px));
    padding: 0;
  }
  .sketch {
    height: 118vw;
    width: 90vw;
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center center;
    margin-bottom: 3vw;
  }
  .sketches1 {
    justify-content: space-between;
    flex-direction: column;
  }
}
</style>
